/*
 * Variables using Custom properties
 */
:root {
  
  /* Colors */
  --black: #000;
  --white: #fff;

  --front: var(--black);
  --back:  var(--white);

  --gray:  #e9e9e9;
  --dark-gray: color( var(--gray) l(50%));
  --rly-dark-gray: color( var(--gray) l(20%));
  
  --primary:     #f05553;
  --second:      #0098d8;
  --third:       #0b3536;
  
  /* typography scale */
  --base-fontsize: 16px;
  --line-height:   calc( 1rem * var(--major-third) );
  --perfect-fifth: calc(3/2); 
  --major-third:   calc(5/4);
  --typoscale: var(--perfect-fifth);
  --linescale: var(--major-third);
    
  /* Default margin/padding */
  --gutter:     0.5em;
  
  /* Header height */
  --header-height: 96px;
  --pad-big: 48px;
  
  /* Shadows */
  --shadow: 0px 0px calc(var(--gutter)*var(--perfect-fifth)) rgba(0,0,0,0.5);
  --shadow-big: 5px 5px calc(4*var(--gutter)*var(--perfect-fifth)) rgba(0,0,0,0.5);
  --shadow-huge: 10px 10px calc(8*var(--gutter)*var(--perfect-fifth)) rgba(0,0,0,0.5);
  
  /* Animations/transitions */
  --trans-props:    all;
  --trans-time:     0.15s;
  --trans-easing:   cubic-bezier(0.52, 0.01, 0.16, 1); /* Expo ease-out */
  
  --trans-time-slow: 0.85s;
  
  /* Reactive variables */
  --scrolly: 1;
}
